<template>
  <div class="getseach">
    <van-nav-bar title="商品列表" left-text="返回" right-text="按钮" left-arrow @click-left="back" />
    <div class="getseach-r">
        <div class="gets" v-for="(item,index) in getserch" :key="index" @click="getgoods(item.goods_id)">
            <div class="fl dolive">
                <div class="doImg">
                    <img class="auto-img" :src="item.goods_small_logo" alt="">
                </div>
                <div class="spam">{{item.goods_name}}</div>
            </div>
