                url: '/goods/search',
                params: {
                    cid:this.cat_id
                }
            }).then(result => {
                if(result.status == 200){
                    this.$toast.clear();
                    this.getserch = result.data.message.goods;
                    // this.goods = result.data.message.goods[0].goods_id;
                };
